.listing {
  width: 25%;
  min-width: 200px;
  max-width: 300px;
  position: relative;
  margin: 20px;
  cursor: pointer;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  
}

.listing::before {
  content: "";
  display: block;
  padding-bottom: calc((5 / 4) * 100%);
}

.listing__content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  background: #C6FFDD;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #f7797d, #FBD786, #C6FFDD);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #f7797d, #FBD786, #C6FFDD); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.listing__image-container {
  width: 100%;
  height: 65%;
  overflow: hidden;
}

.listing__image {
  width: auto;
  height: 100%;
  object-fit: cover;
  
}

.listing__details {
  height: 35%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 0px 25px;
  position: relative;
}

.listing__type {
  position: absolute;
  font-size: 0.8rem;
  right: 10px;
  top: 10px;
  background-color: rgb(223, 247, 255);
  padding: 5px 10px;
  border-radius: 15px;
}

.listing__row {
  margin: 6px 0px;
  display: flex;
  align-items: center;
}

.listing__price {
  font-size: 1.5rem;
}

.listing__address {
  color: #737373;
}
